@import "../../global.scss";

.citySearchBar {
  width: 100%;
  height: $searchBarHeight;
  background-color: $backgroundColor;

  font-size: large;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  z-index: 1;

  form {
    width: 90%;
    z-index: 3;
    box-shadow: 2px 2px 3px 1px #444444;

    .formComponents {
      width: 100%;
      height: 40px;
      border-radius: 5px;
      background-color: $alternativeColor;
      display: flex;
      align-items: center;
      justify-content: space-between;

      input {
        width: 100%;
        height: 90%;
        padding-left: 20px;
        background-color: $alternativeColor;
        color: $textColor;
        border: none;
        outline: none;
      }

      ::placeholder {
        color: #bfbfbf;
        opacity: 1;
      }

      button {
        width: 40px;
        height: 100%;
        padding-right: 10px;
        background-color: $alternativeColor;
        border: none;
        cursor: pointer;

        .magnifyingGlassIcon {
          width: 100%;
          height: 70%;
          display: flex;
          align-items: center;
          justify-content: center;

          g {
            stroke: $textColor;

            path {
              fill: $textColor;
            }
          }
        }

        :hover {
          g {
            stroke: #c6c5c5;

            path {
              fill: #c6c5c5;
            }
          }
        }
      }
    }
  }
}